import RecesPana from "../photos/Recess-pana.svg";
import cssFile from "./LandingPage.css";
import Button from "./UI/Button/Button";

const LandingPage = (props) => {
  const registerHandler = (e) => {
    e.preventDefault();
    props.registerHandler();
  };

  const loginHandler = (e) => {
    e.preventDefault();
    props.loginHandler();
  };

  return (
    <div>
      <h1>Chorify</h1>
      <img src={RecesPana} height={400} width={700} alt="Chorify Family" />
      <Button onClick={registerHandler} className={"register-btn"}>
        Create Family
      </Button>
      <Button onClick={loginHandler} className={"login-btn"}>
        Login
      </Button>
      <footer>
        <a className="credit" href="https://storyset.com/game">
          Game illustrations by Storyset
        </a>
      </footer>
    </div>
  );
};

export default LandingPage;
